body {
  color: #333;
  background-color: #f4f4f4;
  margin: 0;
  padding: 20px;
  font-family: Arial, sans-serif;
}

.container {
  text-align: center;
  max-width: 800px;
  margin: 0 auto;
}

.breed-select {
  width: 100%;
  margin: 20px 0;
  padding: 10px;
}

.loader {
  box-sizing: border-box;
  background: #fff;
  border-radius: 50%;
  width: 16px;
  height: 16px;
  margin: 15px auto;
  animation: 2s linear infinite shadowPulse;
  display: block;
  position: relative;
  box-shadow: -24px 0 #fff, 24px 0 #fff;
}

@keyframes shadowPulse {
  33% {
    background: #fff;
    box-shadow: -24px 0 #ff3d00, 24px 0 #fff;
  }

  66% {
    background: #ff3d00;
    box-shadow: -24px 0 #fff, 24px 0 #fff;
  }

  100% {
    background: #fff;
    box-shadow: -24px 0 #fff, 24px 0 #ff3d00;
  }
}

.hidden {
  display: none;
}

.cat-info {
  background: #e4c8c8;
  border-radius: 10px;
  align-items: flex-start;
  gap: 20px;
  margin-top: 20px;
  padding: 20px;
  display: flex;
  box-shadow: 0 2px 5px #0000001a;
}

.cat-image {
  border-radius: 10px;
  max-width: 300px;
  margin: auto;
  box-shadow: 0 2px 5px #3716161a;
}

.cat-details {
  text-align: left;
}

.error {
  color: red;
  margin-top: 20px;
}
